<template>
  <div id="addBlog">
      <h2>添加博客</h2>
      <form v-if="!submitted">
        <label >博客标题</label>
        <input type="text" v-model="blog.title" required/>
        <label >博客内容</label>
        <textarea v-model="blog.content"></textarea>
      </form>
      <div v-if="submitted">你的博客发布成功</div>
      <hr>
      <div id="checkboxes" v-if="!submitted">
              <label>vue.js</label>
              <input type="checkbox" value="vue.js" v-model="blog.categories"/>
              <label>node.js</label>
              <input type="checkbox" value="node.js" v-model="blog.categories"/>
              <label>react.js</label>
              <input type="checkbox" value="react.js" v-model="blog.categories"/>
              <label>angular.js</label>
              <input type="checkbox" value="angular.js" v-model="blog.categories"/>
            </div>
      <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <ul>
         <li v-for="category in blog.categories">
           {{category}}
         </li>
        </ul>
        <p>作者：{{blog.author}}</p>
      </div>
      <label v-if="!submitted">作者：</label>
      <select v-model="blog.author" v-if="!submitted">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button v-on:click.prevent="post" v-if="!submitted">添加博客</button>
  </div>
</template>

<script>
export default {
  name: 'addBlog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:"",
      },
      authors:["zhangsan","lisi"],
      submitted:false,
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://wd8633350856oxekyx.wilddogio.com/posts.json",
        this.blog
      ).then(function(data){
        console.log(data);
        this.submitted=true;
      })
      ;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #addBlog *{
    border:border-box;
  }
  #addBlog{
    margin:20px auto;
    max-width:600px;
    padding:20px;
  }
  label{
  display:block;
  margin:20px;
  }
  input[type="text"],textarea,select{
    width:100%;
    padding:8px;
  }
  textarea{
  height:200px;}
  #checkboxes label{
  display:inline-block;
  margin-top:0;
  }
  #checkboxes input{
    display:inline-block;
    margin-right:0;
    }
    button{
     display:block;
     margin:20px 0;
     background:crimson;
     color:#fff;
     border:0;
     padding:14px;
     border-radius:4px;
     font-size:18px;
     cursor:pointer;
    }
    #preview{
      padding:10px 20px;
      border:1px dotted #ccc;
      margin:30px 0;
    }
    h3{
      margin-top:10px;
    }
</style>



