<template>
  <div class="test1">
    用户名：<input type="text" v-model="user.userName">
    <button v-on:click="post">保存数据</button>
    <p>{{user.userName}}</p>
  </div>
</template>

<script>
export default {
  name: 'test1',
  data () {
    return {
      user:{
        userName:"",
        password:"",
      }
    }
  },
  methods:{
      post:function(){
        this.$http.post("https://wd4184960023xstjic.wilddogio.com/posts.json",
          this.user
        ).then(function(data){
          console.log(data);

        })
        ;
      }
    }
}
</script>

